import { Container, FormControlLabel, FormGroup, Switch } from "@mui/material";
import React, { useState } from "react";
import ResponsiveAppBar from "./topBar";
import { styled } from "@mui/material/styles";
import { createContext } from "react";
import { useLocation } from "react-router-dom";

export const ShuffleContext = createContext(false);

const Base = ({ children }) => {
    const [shuffled, setShuffled] = useState(false);
    const location = useLocation();

    const Android12Switch = styled(Switch)(({ theme }) => ({
        padding: 8,
        "& .MuiSwitch-track": {
            borderRadius: 22 / 2,
            "&::before, &::after": {
                content: '""',
                position: "absolute",
                top: "50%",
                transform: "translateY(-50%)",
                width: 16,
                height: 16,
            },
            "&::before": {
                backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
                    theme.palette.getContrastText(theme.palette.primary.main)
                )}" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z"/></svg>')`,
                left: 12,
            },
            "&::after": {
                backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
                    theme.palette.getContrastText(theme.palette.primary.main)
                )}" d="M19,13H5V11H19V13Z" /></svg>')`,
                right: 12,
            },
        },
        "& .MuiSwitch-thumb": {
            boxShadow: "none",
            width: 16,
            height: 16,
            margin: 2,
        },
    }));
    return (
        <Container maxWidth="lg" sx={{ p: { xs: "0px" }, mb: 4 }}>
            <ResponsiveAppBar />
            {location.pathname === "/main" && (
                <FormGroup sx={{ p: 2 }}>
                    <FormControlLabel
                        control={
                            <Android12Switch
                                checked={shuffled}
                                onChange={(e) => setShuffled(e.target.checked)}
                            />
                        }
                        label="Shuffle Questions"
                    />
                </FormGroup>
            )}
            <ShuffleContext.Provider value={shuffled}>
                {children}
            </ShuffleContext.Provider>
        </Container>
    );
};

export default Base;
